

























































































































import Vue from 'vue';
import store from '@/store/store';

export default Vue.extend({
  name: 'App',

  data: () => ({
    drawer: false,
  }),

  computed: {
    title() {
      return store.state.main.title;
    },

    menus() {
      return store.state.main.menus;
    },

    confirm() {
      return store.state.confirm;
    },

    snack() {
      return store.state.snack;
    },
  },
});
